@import 'mixins.scss';
@media screen and (max-width:1800px) {
    .sectionInner{
        width: 100%;
        padding: 0 20px;
    }
    
}
@media screen and (max-width:1635px) {
    .sectionHeader_secondRow-modelItem{
        font-size: 16px;
    }
    .sectionHeader_secondRow-buyOptionsItem a{
        font-size: 14px;
    }
    .sectionBannerBestDeal{
        padding: 70px 235px 0 100px
    }
}
@media screen and (max-width:1550px) {
    .sectionBannerBestDeal{
        padding-top: 20px;
    }
    .sectionBannerBestDeal_title{
        // font-size: 60px;
    }
    .sectionBannerBestDeal_header{
        // width: ;
    }
    .sectionBannerBestDeal_itemsList{
        grid-template-columns: 360px;
    }
}
@media screen and (max-width:1420px) {
    .sectionHeader_secondRow-model{
        width: 60%;
        justify-content: flex-start;
        padding-left: 20px;
    }
    .sectionHeader_secondRow-buyOptions{
        width: 40%;
    }
}
@media screen and (max-width:1240px) {
    .sectionBannerBestDeal{
        padding-right: 160px;
        padding-left: 40px;
    }
    .sectionBannerBestDeal_title {
        font-size: 60px;
    }
    .sectionHeader_secondRow-modelItem{
        margin-right: 20px;
    }
    .sectionBannerBestDeal_header{
        width: 430px;
        &:after{
            width: 430px;
        }
    }
    .sectionBannerBestDeal_btn{
        width: 430px;
    }
}
@media screen and (max-width:1024px) {
    .sectionHeader_firstRow-logoImg, .sectionHeader_firstRow-logoSeparator{
        margin-right: 12px;
    }
    .sectionHeader_firstRow-logoDealer{
        width: 120px;
        font-size: 12px;
    }
    .sectionHeader_firstRow-placeAndTime{
        flex-direction: column;
    }
    .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink{
        font-size: 25px;
    }
    .sectionHeader_secondRow-buyOptionsItem{
        margin-right: 40px;
    }
    .sectionBannerAwards{
        padding-left: 0;
    }
    .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after{
        width: 300px;
    }
    .sectionBannerBestDeal_headerText{
        font-size: 20px;
    }
}
@media screen and (max-width:992px) {
    .sectionAdvantages .sectionInner{
        justify-content: space-between;
    }
    .sectionAdvantages_item{
        margin: 0;
    }
    .sectionAdvantages_item-text{
        width: 130px;
    }
}
@media screen and (max-width:880px) {
    .sectionHeader_firstRow-phoneAndBurger{
        width: auto;
    }
    .sectionHeader_firstRow-placeAndTime{
        width: auto;
        // display: none;
        font-size: 12px;
    }
    .sectionHeader_firstRow-phoneAndBurger{
        justify-content: flex-end;
    }
    
    .sectionHeader_firstRow-phoneAndBurger-phone{
        margin-right: 30px;
    }
    .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink{
        font-size: 20px;
    }
    .sectionAdvantages .sectionInner{
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-content: center;
        justify-items: center;
        grid-gap: 30px;
    }
    .sectionAdvantages_item{
        justify-content: flex-start;
    }
    .sectionBannerBestDeal {
        padding-right: 105px;
    }
    .sectionBannerAwards_item {
        width: 225px;
    }

}
@media screen and (max-width:768px) {
    .sectionHeader{
        padding-top: 100px;
    }
    .sectionHeader_firstRow{
        height: 100px;
        align-items: flex-start;
    }
    .sectionHeader_firstRow-placeAndTime{
        position: absolute;
        bottom: 10px;
        left: 20px;

    }
    .sectionHeader_firstRow-phoneAndBurger-phone{
        margin: 0;
        position: absolute;
        bottom: 18px;
        right: 20px;
    }
    .sectionHeader_secondRow{
        display: none;
    }
    .sectionBanner{
        background-position: 65% 100%;
    }
    .sectionBannerAwards{
        display: none;
    }
    .sectionBannerBestDeal{
        width: 100%;
        padding: 20px;
        background: rgba(0, 0, 0, 0.384);
    }
    .sectionBannerBestDeal .wrapper{
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: space-evenly;
    }
    .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after{
        width: 500px;
    }
    .sectionBannerBestDeal_itemsList{
        grid-template-columns: 300px 300px;
        grid-gap: 15px;
    }
    .sectionBannerBestDeal_title{
        text-align: center;
    }
}
@media screen and (max-width:650px) {
    .sectionBannerBestDeal_itemsList{
        grid-template-columns: 300px;
        grid-gap: 30px;
    }
}
@media screen and (max-width:576px) {
    .sectionBannerBestDeal_btn, .sectionBannerBestDeal_header, .sectionBannerBestDeal_header:after{
        width: 250px;
    }
    .sectionAdvantages .sectionInner{
        grid-template-columns: 1fr;
    }
    .sectionHeader_firstRow-logoDealer{
        display: none;
    }
    .sectionHeader_firstRow-phoneAndBurger-phone .phoneLink{
        font-size: 15px;
    }
    .sectionBannerBestDeal_title{
        font-size: 40px;
    }
    .sectionBannerBestDeal_headerText{
        text-align: center;
    }
    .sectionBannerBestDeal_btn{
        font-size: 18px;
    }
    .sectionBannerBestDeal_itemsList-itemText{
        font-size: 18px;
    }
    .sectionHeader_firstRow-placeAndTime{
        left: 10px;
    }
    .sectionHeader_firstRow-phoneAndBurger-phone{
        right: 10px;
    }
}
